<template>
<div>
  <canvas class="my-canvas">抱歉，您的浏览器不支持canvas元素</canvas>
</div>
</template>

<script>
import $ from 'jquery';

export default {
  data() {
    return {
      // ...
    }
  }, // data
  methods: {
    configCanvas() {
      const canvas = $('.my-canvas')[0];
      const ctx = canvas.getContext('2d');
      ctx.fillStyle='#FF0000';
      ctx.fillRect(0,0,100,100);
    }
  }, // methods
  mounted() {
    this.configCanvas();
  }, // mounted
}
</script>

<style scoped>
div {
  text-align: center;
  padding: 50px;
}
.my-canvas {
  border: 1px solid rgb(39, 21, 116);
  height: 100%;
  width: 100%;
}
</style>
